<template>
	<div class="about">
		<!-- 搜索 -->
		<div style="margin-top: 15px;">
			<el-input placeholder="请输入内容" v-model="input3" class="input-with-select">
				<el-select v-model="select" slot="prepend" placeholder="请选择">
					<el-option label="昵称" value="1"></el-option>
					<el-option label="手机号" value="2"></el-option>
				</el-select>
				<el-button slot="append" @click='getone' icon="el-icon-search"></el-button>
			</el-input>
		</div>

		<div class='some-message'>
			<div class=" io">

				<div class="hd"> 总人数 </div>
				<div>{{anypeople}}</div>

			</div>
			<div class=" io">
				<div class="hd"> 总分享币 </div>
				<div>123</div>
			</div>
			<div class=" io">
				<div class="hd"> 总积分 </div>
				<div>456</div>
			</div>


		</div>


		<!-- 列表 -->
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="admin_id" label="ID" width="150">
			</el-table-column>
			<el-table-column fixed prop="admin_names" label="昵称" width="200">
			</el-table-column>
			<el-table-column fixed prop="admin_judge" label="是否商铺" width="100">
			</el-table-column>
			<el-table-column prop="admin_tel" label="联系方式(账号)" width="300">
			</el-table-column>
			<el-table-column prop="admin_consumption" label="消费金额" width="120">
			</el-table-column>
			<el-table-column prop="admin_user_money" label="分享币" width="150">
			</el-table-column>
			<el-table-column prop="admin_user_integral" label="积分余额" width="150">
			</el-table-column>
			<el-table-column prop="" label="操作" width="400">
				<template slot-scope="scope">
					<el-button type="primary" @click='share(scope.$index, scope.row)'>分享币调整</el-button>
					<el-button type="primary" @click='integral(scope.$index, scope.row)'>积分调整</el-button>
					<el-button type="danger" @click='userdelete(scope.$index, scope.row)'>移除该用户</el-button>
				</template>
			</el-table-column>

		</el-table>

		<!-- 分页 -->
		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :page-count='currentPage'>

			</el-pagination>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [], // 数据列表
				input3: '', //搜索框内容
				select: '', //搜索框的东西 1.昵称 2.手机号,
				total: 0,
				currentPage: 1,
				anypeople:''
			}
		},
		methods: {
			// 获取普通用户列表
			init() {
				this.utils.post({
					url: 'userlist'
				}).then(res => {
					res.data.forEach((item) => {
						if (item.admin_judge == 2) {
							item.admin_judge = '是'
						} else {
							item.admin_judge = '否'
						}
					})
					this.tableData = res.data
					this.total = res.total
					this.pageSizes = res.pre_page
				})
			},
			// 选择查询
			getone() {
				if (this.select == '') {
					this.$message.error('请选择查询条件');
				} else if (this.select == 1) {
					let admin_tel = ''
					let admin_names = this.input3
					this.goon(admin_tel, admin_names)
				} else if (this.select == 2) {
					let admin_names = ''
					let admin_tel = this.input3
					this.goon(admin_tel, admin_names)
				}
			},
			// 查询
			goon(admin_tel, admin_names) {
				this.utils.post({
					url: 'search',
					data: {
						admin_tel: admin_tel,
						admin_names: admin_names
					}
				}).then(res => {
					this.tableData = res
				})
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'userlist',
					data: {
						page: e
					}
				}).then(res => {
					res.data.forEach((item) => {
						if (item.admin_user_business == 1) {
							item.admin_user_business = '是'
						} else {
							item.admin_user_business = '否'
						}
					})
					this.tableData = res.data

				})
			},
			// 分享币调整
			share(a, e) {
				this.$prompt('请输入调整后的的数值', '分享币调整', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputErrorMessage: '...'
				}).then(({
					value
				}) => {

					this.utils.post({
						url: 'money',
						data: {
							admin_id: e.admin_id,
							admin_user_money: value
						}
					}).then(res => {
						// js添加
						let a = this.tableData
						a.forEach((item, index) => {
							if (e.admin_id == item.admin_id) {
								item.admin_user_money = value
							}
						})
						this.tableData = a
					})


				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消输入'
					});
				});
			},
			// 积分调整
			integral(a, e) {
				this.$prompt('请输入调整后的的数值', '积分调整', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputErrorMessage: '...'
				}).then(({
					value
				}) => {
					this.global.a(e)
					this.utils.post({
						url: 'integral',
						data: {
							admin_id: e.admin_id,
							admin_user_integral: value
						}
					}).then(res => {
						
						// js添加
						let a = this.tableData
						a.forEach((item, index) => {
							if (e.admin_id == item.admin_id) {
								item.admin_user_integral = value
							}
						})
						this.tableData = a
					})


				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消输入'
					});
				});
			},

			anyother() {
			
				// 总人数
				this.utils.post({
					url:'userman'
				}).then(res=>{
					this.anypeople = res
				})
				// 总分享币
				this.utils.post({
					url:'usermoney'
				}).then(res=>{
					this.anypeople = res
				})
				//总积分 
				this.utils.post({
					url:'userintegral'
				}).then(res=>{
					this.anypeople = res
				})
				

			},

			// 移除用户
			userdelete(a,e){
				this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
							
							this.utils.post({
								url:'userdelete',
								data: {
									admin_id:e.admin_id
								}
							}).then(res=>{
								
								this.tableData.splice(a,1)
								
							})
							
				          this.$message({
				            type: 'success',
				            message: '删除成功!'
				          });
						  
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消删除'
				          });          
				        });
			}
			
		},	
		mounted() {
			this.init()
			this.anyother()
			// window.location.reload()
			// this.$router.go(0)
		}
	}	
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}

	.some-message {
		margin: 20px 0;
		width: 100%;
		height: 100px;
		background: #72edfb;
		display: flex;
		justify-content: space-between;

		.io {
			width: 20%;
			height: 100%;
			line-height: 50px;
			text-align: center;

			.hd {
				font-weight: 600;
			}
		}
	}



	.el-select .el-input {
		width: 130px;

	}

	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
</style>
